<template>
    <div class="report_div">
        <input v-model="reportTitle" placeholder=""/>
        <h2 class="time_h2">{{time}}</h2>
        <div class="cont_wrap" v-for="(article,index) in articleList">
            <input class="cont_num" type="text" v-model="article.order" @blur="sortList">
            <p class="title_p">{{article.title}}</p>
            <img :src="delImg" alt="" @click="delArticle(article,index)">
            <textarea name="" id="" cols="30" rows="10" v-model="article.cont"></textarea>
            <img :src="article.bg_img" class="bg_img" alt="">
        </div>
        <div class="btn_wrap">
            <el-button type="default"></el-button>
            <el-button type="primary"></el-button>
        </div>
    </div>
    
</template>

<style scoped lang="less">
    .report_div {
        width: 80%;
        text-align: left;
        margin: 50px auto 0;

        input {
            width: 100%;
            padding: 0 5px;
            height: 40px;
            line-height: 40px;
            border: 1px solid #eee;
            border-radius: 5px;
            outline: 0;
        }
        .time_h2 {
            margin: 20px 0;
        }
        .cont_wrap {
            margin-bottom: 50px;
            .cont_num {
                text-align: center;
                width: 5%;
                display: inline-block;
            } 
            .title_p {
                width: 88%;
                padding: 0 5px;
                display: inline-block;
                height: 40px;
                line-height: 40px;
                border-radius: 5px;
                border: 1px solid #eee;
                background: #F9FAFC;
            }
            img {
                vertical-align: middle;
            }
            textarea {
                width: 70%;
                padding: 5px;
                height: 170px;
            }
            .bg_img {
                width: 280px;
                height: 180px;
            }
        }
        .btn_wrap {
            text-align: right;
        }
    }
    
	
</style>
<script>
	import {
		ajaxGet,
		ajaxPost,
		getStore,
		setStore,
        containEle
	} from '@/util/util.js'
	import DropdownItem from '@/components/dropItem.vue'
	import SearchItem from '@/components/searchItem.vue'
    import heartOn from 'static/img/heart_on.png'
    import heartOff from 'static/img/heart_off.png'
    import addReport from 'static/img/addReport.png'
    import addReportImg from 'static/img/addReportImg.png'
    import delImg from 'static/img/del.svg'
	export default {
	    data() {
	        return {
    	        reportTitle: '',
                articleList: [],
                time: '',
                delImg: delImg
	        }
	    },
	    components: {
	    	DropdownItem,
	    	SearchItem
	    },
	    created() {
            const id = this.$route.query.id;
	    	this.getReportData(id);
	    },
        watch: {
            
        },   
	    methods: {
	    	getReportData(id){
                const url = '';
                const params = {
                    rpt_id: id
                };
                ajaxPost('/yuqing/get_rpt_dtl',params).then((res)=>{
                    const { ret_code,msg,result } = res.data;
                    this.articleList = result.data;
                    this.time = result.time;
                    this.reportTitle = result.rpt_name;
                });
            },
            delArticle(article,index){
                this.$confirm('','',{
                    confirmButtonText: '',
                    cancelButtonText: '',
                    type: 'warning'
                }).then(()=>{
                    this.articleList.splice(index,1);
                    this.articleList.forEach((value,index)=>{
                        value.order = index + 1;
                    })
                });
                
            },
            sortList(){
                this.articleList.sort(function(a,b){
                    // 
                        return a.order - b.order
                    // }
                })
            }
	    },
        mounted(){
            
        }
	}
</script>


