<template>
    <div class="input-wrap" @click="searchClick($event)">
        <input type="text" v-model="search_val" :placeholder="input_tips">
        <slot name="search_icon"></slot>
        <ul class="search-wrap" v-show="show_dialog">
            <template v-for="(root_item,root_index) in search_arr">
                <li class="root_li" @click="commandClickRoot(root_item,root_index)">{{root_item.name}}</li>
                <template v-if="root_item.children" v-for="(item,index) in root_item.children">
                    <li class="title_li" @click="commandClick(item,index)">{{item.name}}</li>
                </template>
            </template>
        </ul>
    </div>
</template>
<style lang="less">
    .input-wrap {
        width: 200px;
        height: 29px;
        float: right;
        display: inline-block;
        padding: 0 6px;
        box-sizing: border-box;
        position: relative;
        z-index: 10;
        input {
            width: 200px;
            border: 1px solid #cfd2da;
            outline: 0;
            border-radius: 12px;
            font-size: 14px;
            line-height: 29px;
            height: 29px;
            padding-left: 10px;
            box-sizing: border-box;
        }
        img {
            width: 20px;
            height: 20px;
            position: absolute;
            right: 5px;
            top: 13px;
        }
        .search-wrap {
            width: 200px;
            height: 168px;
            overflow-y: auto;
            box-shadow: 0 3px 6px 0 rgba(0,0,0,0.10);
            border-radius: 2px;
            background: #fff;
            position: absolute;
            li {
                width: 100%;
                height: 28px;
                line-height: 28px;
                text-align: left;
                font-size: 13px;
                background: #fff;
                color: #606060;
                border: 0;
                &.root_li {
                    text-indent: 1em;
                }
                &.title_li {
                    text-indent: 2em;
                    &:hover {
                        background: #f5f5f5;
                        cursor: pointer;
                    }
                }
            }
        }
    }
</style>
<script>
    import { containEle,throttle } from '@/util/util.js'
    export default {
        props: {
            search_arr: {
                type: Array,
                default: []
            },
            show_dialog: {
                type: Boolean,
                default: false
            },
            input_tips: {
                type: String,
                default: ''
            },
            prop_search_val: {
                type: String,
                default: ''
            }
        },
        data(){
            return {
                search_val: '',
                // 
                isClick: true,    //div
                isSearch: false,  //
                isChoose: false,  //li
            }
        },
        watch:{
            'search_val': throttle(function(newVal) { 
               this.isChoose ? this.isSearch = false : this.isSearch = true;
               this.$emit('changeSearch',newVal.toLowerCase(), this.isSearch);
            }, 500),
        },
        methods: {
           searchClick(event){
               // 
               if(event.target.nodeName == 'INPUT'){
                   this.isChoose = false;
                   this.isSearch = false;
                   this.$emit('changeSearch',this.search_val,this.isClick);
               }
           },
           commandClick(item,index){
               this.$emit('changeCommand',item,index);
           },
           commandClickRoot(item,index){
               this.search_val = item.name;   
               this.$emit('changeCommandRoot',item,index,this.isChoose);
               this.isChoose = true;                                           
            }
        }
    }
</script>


